.game-container {
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: right;
  width: 90%;
  height: 100%;
  max-width: 800px;
  background-color: #243B53;
}

.statistics-container {
  display: flex;
  justify-content: right;
  align-items: center;
}

.score {
  display: flex;
  justify-content: right;
  align-items: center;
  margin: 5px 30px;
  font-size: 36px;
  color: white;
}

.card-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  margin: 10px;
}