.header {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-items: center;
  height: 100%;
  width: 100%;
  max-width: 800px;
  min-height: 4em;
  background-color: #334e68;
}

.header-buttons {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;

}

.changeGame {
  display: flex;
  justify-content: space-around;
  align-items: right;
  font-size: 12px;
}